* {
  box-sizing: border-box;
}

html {
  font-family: Helvetica, sans-serif;
}

body {
  margin: 0;
}

ul,
li,
h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}

iframe {
  display: none;
}

.App {
  background-color: lightcyan;
}

.App__title {
  text-align: center;
  font-size: 40px;

  padding: 20px;
}
